@tailwind base;
@tailwind components;
@tailwind utilities;
html {
  transition: background-color 0.3s ease, color 0.3s ease;
}
body {
  background-image: url("gotham.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #fff;
  background-size: cover;
  opacity: 0.9;
  z-index: 0;
}

/* Overlay sombre */
body::before {
  content: '';
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6); /* ajuste ici pour rendre plus ou moins sombre */
  z-index: -1;
  pointer-events: none; /* Ã©vite de bloquer les clics */
}

button.bg-yellow-500 {
  color: black !important;
}
